const NFTDetail = () => {
  const web3 = useWeb3()
  const { getTokensNftDetail } = useApi()
  const [isCanceling, setIsCanceling] = useState(false)
  const [auctionInfo, setAuctionInfo] = useState({
    startingPrice: null,
    endingPrice: null,
    duration: null,
    seller: null,
    startedAt: null,
  })
  const [isLoadingNftInfo, setIsLoadingNftInfo] = useState(true)
  const [isButtonLoading, setIsButtonLoading] = useState(false)
  const [token, setToken] = useState(null)
  const { slowRefresh, fastRefresh } = useRefresh()
  const [currentPrice, setCurrentPrice] = useState('~')
  const { MARKETPLACE = {}, CYBLOC_CORE = {} } = useSelector(selectContractAddress)
  const { onBuyNft } = useBuyNft(MARKETPLACE.ADDRESS, CYBLOC_CORE.ADDRESS)
  const { onGetCurrentAuction, onGetCurrentPrice } = useGetAuction(MARKETPLACE.ADDRESS, CYBLOC_CORE.ADDRESS)
  const payTokenContract = useMemo(() => {
    return getBep20Contract(get(token, 'payToken.address', null), web3)
  }, [token, web3])
  const { onApprove } = useApprove(payTokenContract, MARKETPLACE.ADDRESS)
  const [isApproved, fetchAllowanceData, isLoadingApproveStatus] = useIsApproved(payTokenContract, MARKETPLACE.ADDRESS)
  const { nftId } = useParams()
  const { onCanceledSell } = useCancelledSellPackage(MARKETPLACE.ADDRESS)

  const [auctionExist, setAuctionExist] = useState(true)
  const [successBuyOrCancel, setSuccessBuyOrCancel] = useState(false)

  const { account } = useWeb3React()

  const payToken = useMemo(() => {
    return get(token, 'payToken', {})
  }, [token])

  const startingPrice = useMemo(() => {
    return get(token, 'startingPrice', 0)
  }, [token])

  const endingPrice = useMemo(() => {
    return get(token, 'endingPrice', 0)
  }, [token])
  const fetchNFTDetail = async () => {
    const res = await getTokensNftDetail(nftId)
    if (res && res.results && res.results.length !== 0) {
      setToken(res.results[0])
      setIsLoadingNftInfo(false)
    }
  }
  const fetchAuctionInfo = async () => {
    try {
      const result = await onGetCurrentAuction(nftId)
      if (result) {
        const { decimals } = payToken
        const { duration, endingPrice, startingPrice, seller, startedAt } = result
        setAuctionInfo({
          startingPrice: getBalanceAmount(startingPrice, decimals).toString(),
          endingPrice: getBalanceAmount(endingPrice, decimals).toString(),
          seller,
        })

        if (seller === '0x0000000000000000000000000000000000000000') {
          setAuctionExist(false)
        }
        setIsLoadingAuctionInfo(false)
      }
    } catch (error) {
      setIsLoadingAuctionInfo(false)

      console.log(error, 'fail to fetch ?')
    }
  }

  const fetchCurrentPrice = async () => {
    try {
      const result = await onGetCurrentPrice(nftId)
      if (result) {
        const decimals = get(payToken, 'decimals', null)
        const newPrice = getBalanceAmount(result, decimals).toFixed(2)
        if (newPrice && newPrice !== 'NaN') {
          setCurrentPrice(newPrice)
        }
      }
    } catch (error) {
      console.log(error, 'fail to get current price')
    }
  }

  const onHandleBuyNft = useCallback(async () => {
    try {
      setIsButtonLoading(true)
      const payTokenAddress = get(payToken, 'address', null)
      // If user doesn't approve token spend we will ask them for their permission
      if (!isApproved && payTokenAddress !== ZERO_ADDRESS) {
        const res = await onApprove()
        if (!res) {
          setIsButtonLoading(false)
          return
        }
        fetchAllowanceData()
      }
      const decimals = get(payToken, 'decimals', null)
      const payAmount = getDecimalAmount(currentPrice, decimals).toString()
      await onBuyNft(nftId, payAmount, payTokenAddress)
      setIsButtonLoading(false)
      setSuccessBuyOrCancel(true)
    } catch (error) {
      console.log(error, 'error ?')
      setIsButtonLoading(false)
    }
  }, [payToken, currentPrice, isApproved])

  useEffect(() => {
    fetchNFTDetail()
  }, [])

  useEffect(() => {
    if (MARKETPLACE.ADDRESS && CYBLOC_CORE.ADDRESS) {
      fetchCurrentPrice()
    }
  }, [MARKETPLACE, CYBLOC_CORE, fastRefresh])

  useEffect(() => {
    if (MARKETPLACE.ADDRESS && CYBLOC_CORE.ADDRESS && token) {
      fetchAuctionInfo()
    }
  }, [MARKETPLACE, CYBLOC_CORE, token])

  const NFT_DETAIL_CONFIG = useMemo(() => {
    if (token) {
      return NFT_COLORS[token.type]
    }
    return {}
  }, [token])

  const handelCancelAution = async () => {
    try {
      setIsCanceling(true)
      const result = await onCanceledSell(CYBLOC_CORE.ADDRESS, token.cyblocId)
      setIsCanceling(false)
      setSuccessBuyOrCancel(true)
      toast.success(`Successfully create auction for CyBloc #${nftId}`)
    } catch (error) {
      toast.error('Fail to sell NFT')
      setIsLoading(false)
    }
  }
  return (
    <Wrapper>
      {isLoadingNftInfo ? (
        <LoaderContainer />
      ) : (
        <>
          <Header>
            <Box>
              {!token ? (
                <></>
              ) : (
                <PrimaryText mb="5px">
                  #{token.cyblocId} - {NFT_DETAIL_CONFIG.name}
                </PrimaryText>
              )}
              {!token ? (
                <></>
              ) : (
                <Flex mt="4px" alignItems="center">
                  <SubText>Owned by - </SubText>)
                  <SubText style={{ color: '#FFE5BE', fontSize: '1.2rem' }}>{token.seller}</SubText>
                </Flex>
              )}
            </Box>
            <Flex alignItems="center">
              {isLoadingAuctionInfo ? (
                <Button>
                  <LoaderIcon margin="4px" />
                </Button>
              ) : account && token && account === token.seller ? (
                <Button onClick={handelCancelAution} disabled={!auctionExist || successBuyOrCancel}>
                  {isCanceling ? <LoaderIcon margin="4px" /> : <SubText fontSize="2rem">Cancel Auction</SubText>}
                </Button>
              ) : (
                <Button onClick={onHandleBuyNft} disabled={!auctionExist || successBuyOrCancel}>
                  {isButtonLoading ? <LoaderIcon margin="4px" /> : <SubText>Buy now</SubText>}
                </Button>
              )}

              <ShareButton src="images/ShareNetwork.png" />
            </Flex>
          </Header>
          {/* check auction exist display price */}
          {auctionExist ? (
            <>
              <Flex mt="20px">
                <SubText>Starting price - </SubText>)
                <SubText style={{ color: '#FFE5BE' }}>
                  {!isLoadingNftInfo ? startingPrice : '~'} {payToken.symbol}
                </SubText>
              </Flex>
              <Flex mt="20px">
                <SubText>Ending price - </SubText>)
                <SubText style={{ color: '#FFE5BE' }}>
                  {!isLoadingNftInfo ? endingPrice : '~'} {payToken.symbol}
                </SubText>
              </Flex>
              <Flex mt="20px">
                <SubText>Current price - </SubText>)
                <SubText style={{ color: '#FFE5BE' }}>
                  {!isLoadingNftInfo ? currentPrice && currentPrice.toFixed(2) : '~'} {payToken.symbol}
                </SubText>
              </Flex>
            </>
          ) : (
            <Flex width="100%" justifyContent="center" margin="50px auto">
              {' '}
              <SubText style={{ color: '#AC4040' }}>This auction is not available.</SubText>{' '}
            </Flex>
          )}

          <StyledFlex mt="20px">
            <CyblocImageWrapper>{!token ? <></> : <CyBlocCard data={token} />}</CyblocImageWrapper>
            <CyblocInfoWrapper>
              <Bg>
                <StyledHeaderText top="-6px">Skills</StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />
                <Flex alignItems="center" justifyContent="center" width="100%" mt="11px">
                  {!token ? (
                    <></>
                  ) : (
                    <>
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="50px" height="50px">
                        <SkillMainText>{token.skillOverall}</SkillMainText>
                      </NumberFrame>
                      <Flex flexDirection="column" ml="10px">
                        <SkillMainText mb="5px">Overall</SkillMainText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillOverall} />
                        </StyledInput>
                      </Flex>
                    </>
                  )}
                </Flex>

                <Flex justifyContent="space-between" margin="12px">
                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillShooting}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Shooting</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillShooting} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}
                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillCrossing}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Crossing</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillCrossing} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}
                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillPhysical}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Physical</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillPhysical} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}
                </Flex>
                <Flex justifyContent="space-between" margin="12px">
                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillDribbling}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Dribbling</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillDribbling} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}

                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillPassing}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Passing</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillPassing} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}

                  {!token ? (
                    <></>
                  ) : (
                    <StyledSkillInfoBlock alignItems="center" justifyContent="center">
                      <NumberFrame url={NFT_DETAIL_CONFIG.numberFrame} width="39px" height="39px">
                        <SkillSubText>{token.skillTackling}</SkillSubText>
                      </NumberFrame>
                      <InputFrame flexDirection="column">
                        <SkillSubText mb="5px">Tackling</SkillSubText>
                        <StyledInput>
                          <StyledPercentage percentage={token.skillTackling} />
                        </StyledInput>
                      </InputFrame>
                    </StyledSkillInfoBlock>
                  )}
                </Flex>
              </Bg>

              <Bg mt="20px">
                <StyledHeaderText top="-6px">Traits</StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />
                <Flex flexDirection="column" margin="30px 10px 12px 10px">
                  {!token ? (
                    <></>
                  ) : (
                    token.traits.map(({ type: traitType, id }, index) => {
                      return (
                        <Flex key={id} alignItems="center" mb="5px">
                          <TraitImage src={TRAIT_ICON_MAP[traitType]} />
                          <SubText width="25%">Trait {index + 1}</SubText>
                          <MiniText>
                            {TRAIT_MAP[traitType]} #{id}
                          </MiniText>
                        </Flex>
                      )
                    })
                  )}
                </Flex>
              </Bg>

              <Bg mt="20px" style={{ minHeight: '83px' }}>
                <StyledHeaderText top="-6px">Mentor</StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />
                <Flex justifyContent="center" mt="35px">
                  <MiniText>No Mentors</MiniText>
                </Flex>

                {/* <Box style={{ margin: '30px 10px 14px 40px', position: 'relative' }}>
                  <MiniText>No Mentors</MiniText>
                </Box> */}
              </Bg>
              <Bg mt="20px" style={{ minHeight: '83px' }}>
                <StyledHeaderText top="-6px">Mentees</StyledHeaderText>
                <ClippedWrapper
                  bgColor={NFT_DETAIL_CONFIG.backgroundColor}
                  bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
                />

                <Flex justifyContent="center" mt="35px">
                  <MiniText>No Mentees</MiniText>
                </Flex>
              </Bg>
            </CyblocInfoWrapper>
          </StyledFlex>
        </>
      )}

      {!token ? (
        <></>
      ) : (
        <Activity
          cyblocId={nftId}
          bgColor={NFT_DETAIL_CONFIG.backgroundColor}
          bgColorAfter={NFT_DETAIL_CONFIG.backgroundColorAfter}
        />
      )}
    </Wrapper>
  )
}

export default NFTDetail
