import React, { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Dropdown from 'components/Dropdown'
import LoaderIcon from 'components/LoaderIcon'
import { getDecimalAmount } from 'utils/formatBalance'
import { ReactComponent as InfoIcon } from 'assets/images/Info.svg'
import { SubText, InputRightWrapper, InputRight, BaseText, Button, ErrorText } from './styles'

const FirstTab = ({ payTokens, onHandleListing, isLoading, marketFee }) => {
  const [coinValue, setCoinValue] = useState(null)
  const [priceError, setPriceError] = useState(false)
  const [price, setPrice] = useState('')

  useEffect(() => {
    if (payTokens && payTokens.length) {
      setCoinValue(payTokens[0])
    }
  }, [])

  const onListing = () => {
    if (price) {
      const { decimals, address } = coinValue
      const payAmount = getDecimalAmount(price, decimals)
      onHandleListing(payAmount.toString(), payAmount.toString(), address, '604800')
      setPriceError(false)
    } else {
      setPriceError(true)
    }
  }

  return (
    <>
      <SubText mb="12px">Price</SubText>
      <Flex mb="24px">
        <Dropdown
          options={payTokens}
          wrapperWidth="30%"
          value={coinValue}
          onHandleChange={(value) => {
            setCoinValue(value)
          }}
        />
        <Box width="60%">
          <InputRightWrapper>
            <InputRight placeholder="Amount" type="number" value={price} onChange={(e) => setPrice(e.target.value)} />
          </InputRightWrapper>
          {priceError && (
            <ErrorText ml="25px" mt="7px">
              This field is required
            </ErrorText>
          )}
        </Box>
      </Flex>
      <Flex alignItems="center" mb="24px">
        <SubText mr="5px">Fees</SubText>
        <InfoIcon />
      </Flex>
      <Flex justifyContent="space-between" mb="10px">
        <BaseText fontSize="12px">Service fee</BaseText>
        <BaseText fontSize="12px">{marketFee}%</BaseText>
      </Flex>
      <Flex justifyContent="space-between" mb="24px">
        <BaseText fontSize="12px">Creator royalties</BaseText>
        <BaseText fontSize="12px">7%</BaseText>
      </Flex>
      <Flex justifyContent="center">
        <Button onClick={onListing}>
          {isLoading ? <LoaderIcon /> : <BaseText fontSize="2rem">FINISH LISTING</BaseText>}
        </Button>
      </Flex>
    </>
  )
}

export default FirstTab
